:root {
  --primary-color: #007bff;
  --secondary-color: #ddd;
  --shadow-color: rgba(0, 0, 0, .3);
}

body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.search-form input[type="text"] {
  border: 1px solid var(--secondary-color);
  border-radius: 4px;
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-items: center;
  display: grid;
}

.photo-card {
  border: 1px solid var(--secondary-color);
  box-shadow: 0 0 5px var(--shadow-color);
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: auto;
}

.info {
  background-color: #f9f9f9;
  padding: 10px;
}

.info-item {
  color: var(--primary-color);
  margin: 5px 0;
}

.load-more {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  display: block;
}

.sl-image {
  background-color: rgba(0, 0, 0, .9);
  justify-content: center;
  align-items: center;
  max-width: 100%;
  height: 100%;
  max-height: 80vh;
  margin: 0 auto;
  display: flex;
}

